.nav {
    position                  : fixed;
    top                       : 0;
    display                   : flex;
    justify-content           : space-between;
    width                     : 100%;
    padding                   : 20px;
    /* background-color       : #111; */
    height                    : 30px;
    z-index                   : 1;
    transition-timing-function: ease-in;
    transition                : all 0.5s;
}

.nav-logo {
    position  : fixed;
    left      : 20px;
    width     : 80px;
    object-fit: contain;
}

.nav-avatar {
    position     : fixed;
    right        : 20px;
    width        : 35px;
    object-fit   : contain;
    border-radius: 50%;
}

.nav_black {
    background-color: #111;
    transition      : 0.2s;
}